.NotFound {
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  display: flex;

  height: 100vh;
  background-image: url("../../assets/images/not-found-bg.jpg");

  animation: fadeIn 300ms ease-in;

  .content {
    display: flex;
    flex-direction: row;
    margin: auto;

    width: 70%;
    min-width: 850px;
    height: 70%;

    // background-color: white;

    .content-bg {
      height: 100%;
      object-fit: contain;
      background-color: lightyellow;
      box-shadow: 0 0 10px black;
      z-index: 1;
    }
    .content-description {
      height: 50%;
      width: 100%;
      margin: auto 0 auto 0;
      box-shadow: 0 0 8px rgb(82, 82, 82);
      background-color: white;
      border-radius: 0 3px 3px 0;
      padding: 16px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      h1 {
        font-family: Lato-Bold;
        color: #724a0e;
        letter-spacing: 6px;
        margin: 16px;
      }
      h2 {
        font-size: 1.5rem;
        font-family: Lato-Thin;
        font-weight: bold;
        color: #78541d;
        letter-spacing: 3px;
        margin: 6px;
      }
      p {
        font-family: Lato-Thin;
        font-weight: bold;
        color: #724a0e;
        text-align: center;
        letter-spacing: 3px;
        margin: 6px;
      }

      .button {
        margin: 16px;
        padding: 8px;

        border-radius: 8px;
        background-color: #dcb374;

        p {
          color: white;
          text-transform: uppercase;
          font-size: 0.9rem;
          user-select: none;
        }
      }
      .button:hover {
        opacity: 0.8;
        transition: 300ms;
        cursor: pointer;
      }
      .button:active {
        transform: scale(0.97);
        transition: 300ms;
      }
    }
  }
}
