.Footer {
  margin-bottom: 20px;

  span {
    font-size: 0.95rem;
    color: white;
  }

  .name {
    text-transform: uppercase;
    text-decoration: underline;
    // font-weight: bold;
    letter-spacing: 1.5px;
    font-weight: 500;
  }

  .name:hover {
    color: #ffaa00;

    transition: 300ms;
    cursor: pointer;
  }
}
