.Characters {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  background-color: rgb(10, 10, 10);

  display: flex;
  flex: 1;
  flex-direction: column;

  .background-image {
    position: absolute;

    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    min-height: 600px;
    height: 100%;
    // height: auto;

    img {
      object-fit: cover;

      width: 100%;
      height: 100%;
    }

    .bg-gradient {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.85);
      // width: 100%;
      height: 100%;
    }
  }

  .character-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    flex: 1;

    .selected-container {
      position: absolute;
      right: 0;
      left: 0;
      margin: 0 auto 0 auto;

      display: flex;

      width: 90%;
      height: 65%;

      background-color: rgba(255, 255, 255, 0.493);
      border-radius: 3px;

      box-shadow: 0 0 10px black;

      .profile-container {
        width: 60%;
        height: 100%;

        // animation: fadein 1s ease-in forwards;

        img {
          // min-width: 700px;
          width: 100%;
          height: 100%;
          // object-position: 0 0;
          border-radius: 3px 0 0 3px;
          object-fit: cover;
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        }
      }

      .content {
        max-width: 40%;
        max-height: 100%;
        overflow-y: auto;
        padding: 3% 24px 3% 24px;

        .username {
          color: rgb(255, 255, 255);
          // color: #3b2a04;
          font-size: 2rem;
          text-transform: uppercase;
          text-align: center;
          font-family: Blinker;
          font-weight: bold;
        }

        .role {
          color: rgb(255, 255, 255);
          text-align: center;
          text-transform: uppercase;
          font-size: 1.1rem;
          letter-spacing: 3px;
        }

        .socials-container {
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .social {
          margin: 12px;
          opacity: 0.5;
        }
        .social:hover {
          opacity: 1;
          transform: scale(0.95);
          transition: 300ms;
          cursor: pointer;
        }
        .social:active {
          transform: scale(0.9);
          transition: 300ms;
        }

        .lore-title {
          font-size: 1.8rem;
          margin: 24px 0 12px 0;
          color: #24221d;
        }

        .lore-description {
          // font-size: 1.2rem;
          letter-spacing: 3px;
          line-height: 24pt;
          color: #1d2124;
          // color: white;
          opacity: 0.8;
          text-align: justify;
          white-space: pre-line;
          word-spacing: 3px;
          font-family: Lato-Regular;
        }

        h1 {
          letter-spacing: 3px;
          font-family: Lato-Bold;
        }
        p {
          font-family: Lato-Regular;
        }
      }
    }

    .unselected-container {
      position: absolute;
      color: rgba(255, 255, 255, 0.3);
      margin: auto;
      left: 0;
      right: 0;
      bottom: calc(
        150px + 20px
      ); //150px is the total of carousel from bottom + 20px extras
      text-align: center;
      align-items: center;
      animation: fade-in 300ms ease-in;

      h1 {
        font-size: 1rem;
        text-transform: uppercase;
        font-family: Lato-Regular;
        letter-spacing: 3px;
        word-spacing: 3px;
        // color: white;
      }

      .arrow {
        align-self: center;
        // border: 1px solid #1d2124;
        border-radius: 100%;
        // padding: 16px;
        width: 30px;
        height: 30px;
        animation: bounce 2s ease-in-out infinite alternate;
      }
    }

    .carousel-parent-container {
      margin: auto 0 30px 0;
      width: 100%;
    }
  }

  .carousel {
    // margin-bottom: auto;
    margin-top: auto;

    color: black;
    padding: 0;
  }
  .carousel-item {
    display: flex;
    width: 100px;
    height: 120px;

    border-radius: 3px;
    margin: 0px 12px 0px 12px;
  }

  .carousel-item:hover {
    opacity: 0.75;
    transition: 250ms;
    cursor: pointer;
  }

  .carousel-item:active {
    transform: scale(0.9);
    transition: 250ms;
  }

  .fade-enter {
    opacity: 0;
    z-index: 1;
  }

  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
  }

  .fade-exit {
    opacity: 1;
  }

  .fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 300ms ease-in;
  }

  .fade-exit-done {
    opacity: 0;
  }

  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes bounce {
    0% {
      transform: translateY(0) rotate(90deg);
    }
    50% {
      transform: translateY(10px) rotate(90deg);
    }
    100% {
      transform: translateY(0) rotate(90deg);
    }
  }

  /** SCROLLBAR HANDLER */
  /* width */
  ::-webkit-scrollbar {
    width: 8px;
  }

  /* button */
  ::-webkit-scrollbar-button {
    // background: white;
    // border-radius: 0 0 3px 3px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgba(51, 51, 51, 0.9);
    border-radius: 0 1px 1px 0;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #666;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #000;
  }

  /* Corner */
  ::-webkit-scrollbar-corner {
    background: #999;
  }

  /* Resizer */
  ::-webkit-resizer {
    background: #111;
  }
}
