@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Home {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgb(0, 0, 0);

  height: 100vh;

  .carousel {
    animation: fadeIn 1s ease-in;
  }

  .bg-gradient {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    height: 100vh;
    background: linear-gradient(
      rgba(0, 0, 0, 0.3) 2%,
      transparent 80%,
      rgba(0, 0, 0, 0.7)
    );
  }

  .carousel-control-prev,
  .carousel-control-next {
    margin-top: 60px;
  }

  h3 {
    // text-transform: uppercase;
    font-size: 1.5rem;
    letter-spacing: 3px;
    font-family: Lato-Regular;
    font-weight: bold;
  }
  p {
    letter-spacing: 3px;
  }
}
