.ChapterItem { 
    display: flex;
    width: 100%;
    height: 100px;
    background-color: white;
    border-radius: 8px;

    img { 
        width: 200px;
        height: 100%;
        object-fit: cover;
        border-radius: 8px 0 0 8px;
    }

    .content { 
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: space-between;
        padding: 20px 40px 20px 40px;

        h1 {
            font-size: 1.3rem;
            font-family: Lato-Regular;
            letter-spacing: 6px;
        }
        h2 { 
            font-size: 1rem;
            font-family: Lato-Thin;
            font-weight: bold;
            letter-spacing: 6px;
        }
        h3{ 
            font-size: 1rem;
            font-family: Lato-Thin;
            font-weight: bold;
        }
    }
}