.ChapterDetail {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: white;

  overflow: auto;

  .content {
    max-width: 50%;
    // min-width: 800px;
    margin: 0 auto 0 auto;

    padding: 100px 0 100px 0;

    .imageContainer {
      position: relative;
    }

    .content-image {
      width: 100%;
      object-fit: contain;
      user-drag: none;
      user-select: none;
      -moz-user-select: none;
      -webkit-user-drag: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      pointer-events: none;
    }
  }

  .ToTopButton {
    position: fixed;
    bottom: 0;
    right: 0;
    margin: 0 40px 20px 0;

    height: 70px;
    width: 70px;
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    padding: 8px;

    border: 1px solid rgb(207, 207, 207);

    border-radius: 50%;
    opacity: 1;
    // transform: rotate(270deg);
  }

  .ToTopButton:hover {
    background-color: rgb(230, 230, 230);
    transition: 300ms;
    cursor: pointer;
  }

  .fade-out {
    pointer-events: none;
    opacity: 0;
    transition: 300ms;
  }

  .fade-in {
    opacity: 1;
    transition: 300ms;
  }
}
