// @import "../../themes/theme.scss";

.About {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  overflow-y: auto;

  // background-color: $primary-color;
  background-image: url("../../assets/images/about-bg.jpg");
  background-color: black;
  background-size: cover;
  height: 100%;

  .content {
    // margin: 24px 0 0 0;
    min-width: 65%;
    // max-width: 550px;

    h1 {
      font-family: Lato-Regular;
      letter-spacing: 6px;

      margin: 0 0 20px 0;
      border-bottom: 5px solid #ffc875;
    }

    .description {
      font-size: 1.2rem;
      font-family: Lato-Regular;
      letter-spacing: 3px;
      word-spacing: 3px;
      line-height: 36pt;
      margin-top: 48px;
    }
    .social {
      display: flex;

      flex-direction: row;

      align-content: center;

      margin-top: 20px;

      .user {
        font-size: 1.2rem;
        font-family: Lato-Bold;
        letter-spacing: 3px;
      }
      .divider {
        height: 32px;
        margin: 0 8px 0 8px;
        width: 1px;
        background-color: white;
      }

      .social-button:hover {
        filter: brightness(60%) sepia(1);
        cursor: pointer;
        transition: 300ms;
      }
    }
  }

  .highlight {
    color: #ffc875;
  }
}
