.ChapterList {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100vh;

  padding-top: 80px;

  overflow-y: auto;

  background-image: url("../../assets/images/chapters-bg.jpg");
  background-size: cover;
  background-position: 0px 0px;

  .chapter_item {
    margin: 20px 30px 20px 30px;
  }
  .chapter_item:hover {
    opacity: 0.8;
    cursor: pointer;
    transition: 250ms;
  }
  .chapter_item:active {
    transform: scale(0.98);
    transition: 250ms ease-in;
  }
}
